import { createWebHistory, createRouter } from "vue-router";

import main from "@/views/main.vue";
import notfound from "@/views/404.vue";
import loshu from "@/views/loshu.vue";
import alex from "@/views/alex.vue";
import planet from "@/views/planet.vue";
import prog from "@/views/prog.vue";
import roman from "@/views/roman.vue";
import shanov from "@/views/shanov.vue";

const routes = [
    {
        path: "/",
        name: "Основная страница",
        component: main,
    },
    {
        path: "/loshu",
        name: "Ло Шу",
        component: loshu,
    },
    {
        path: "/alex",
        name: "Матрица Александрова",
        component: alex,
    },
    {
        path: "/planet",
        name: "Планеты",
        component: planet,
    },
    {
        path: "/prog",
        name: "Прогноз",
        component: prog,
    },
    {
        path: "/roman",
        name: "Романова",
        component: roman,
    },
    {
        path: "/shanov",
        name: "Хшановская",
        component: shanov,
    },
    {
        path: "/*",
        name: "404",
        component: notfound,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;
