const state = () => ({
  social: [ '', '', '', '', '', ''],
  whiteKarma: [ '', '', '', ''],
  grayKarma: [ '', '', '', ''],
  periods: [ '', '', '', ''],
  blackKarma: [ '', '', '', ''],
  allArcans: [],
  planets: ['♂', '☉', '☽', '♃', '♅', '☿', '♀', '♆', '♄', '♂'],
  planetsArr: [],
  ageGap: 0,
  ages: [],
  arcanNames: [
    { name: "ошибка" },
    { name: "Маг" },
    { name: "Верховная жрица" },
    { name: "Императрица" },
    { name: "Император" },
    { name: "Верховный жрец" },
    { name: "Влюбленные" },
    { name: "Колесница" },
    { name: "Правосудие" },
    { name: "Отшельник" },
    { name: "Колесо Фортуны" },
    { name: "Сила" },
    { name: "Повешенный" },
    { name: "Смерть" },
    { name: "Умеренность" },
    { name: "Дьявол" },
    { name: "Башня" },
    { name: "Звезда" },
    { name: "Луна" },
    { name: "Солнце" },
    { name: "Суд" },
    { name: "Мир" },
    { name: "Глупец" }
  ]
});

const getters = {
  getSocial: state => state.social,
  getWhite: state => state.whiteKarma,
  getGray: state => state.grayKarma,
  getBlack: state => state.blackKarma,
  getPeriods: state => state.periods,
  getAllArcans: state => state.allArcans,
  getPlanets: state => state.planets,
  getPlanetsArr: state => state.planetsArr,
  getAgeGap: state => state.ageGap,
  getAges: state => state.ages
};

const mutations = {
  SET_SOCIAL (state, payload) {
    state.social = payload
  },
  SET_WHITE (state, payload) {
    state.whiteKarma = payload
  },
  SET_GRAY (state, payload) {
    state.grayKarma = payload
  },
  SET_PERIODS (state, payload) {
    state.periods = payload
  },
  SET_BLACK (state, payload) {
    state.blackKarma = payload
  },
  SET_ALLARCANS (state, payload) {
    state.allArcans = payload
  },
  SET_PLANETSARR (state, payload) {
    state.planetsArr = payload
  },
  SET_AGE_GAP (state, payload) {
    state.ageGap = payload
  },
  SET_AGES (state, payload) {
    state.ages = payload
  }
};

const actions = {
  setSocial({ commit }, payload) {
    let bdayArr = payload.split('.')
    let day = bdayArr[0]
    let month = bdayArr[1]
    let year = bdayArr[2]
    let social = ['', '', '', '', '', '']
    social[0] = minusTT(day)
    social[1] = parseInt(month, 10)
    social[2] = minusTT(addAllCiphers(year))
    social[3] = minusTT(parseInt(social[0], 10) + parseInt(social[1], 10) + parseInt(social[2], 10))
    social[4] = minusTT(addAllNums(addAllCiphers(parseInt(day,10)) + addAllCiphers(parseInt(month,10)) + addAllCiphers(parseInt(year,10))))
    social[5] = minusTT(addAllCiphers(addAllCiphers(parseInt(day, 10)))+addAllCiphers(parseInt(month, 10))+addAllCiphers(addAllCiphers(parseInt(year,10))))
    commit('SET_SOCIAL', social)
  },
  setKarma({ commit }, payload) {
    let bdayArr = payload.split('.')
    let day = bdayArr[0]
    let month = bdayArr[1]
    let year = bdayArr[2]
    let card1 = dateCard(day)
    let card2 = monthCard(month)
    let card3 = yearCard(year)
    let white0 = minusTT(card1 + addAllCiphers(day, month, year))
    let white1 = minusTT(card1 + addAllNums(card1, card2, card3))
    let white2 = minusTT(minusTT(parseInt(card1, 10) + parseInt(card2, 10)) + minusTT(parseInt(card2, 10) + parseInt(card3, 10)))
    let white3 = minusTT(card2 + white2)
    let gray0 = minusTT(Math.abs(card1 - minusTT(addAllCiphers(day, month, year))))
    let gray1 = minusTT(Math.abs(card1 - minusTT(addAllNums(card1, card2, card3))))
    let gray2
    let gray3

    if (9 < parseInt(day) && parseInt(day) < 22) {
      gray2 = minusTT(parseInt(day) + parseInt(month))
      gray3 = parseInt(day)
    } else {
      gray2 = minusTT(parseInt(day) + parseInt(month))
      gray3 = "НЕТ"
    }

    let white = [white0, white1, white2, white3]
    let gray = [gray0, gray1, gray2, gray3]
    commit('SET_WHITE', white)
    commit('SET_GRAY', gray)
  },
  setPeriods({ commit }, payload) {
    let bdayArr = payload.split('.')
    let day = bdayArr[0]
    let month = bdayArr[1]
    let year = bdayArr[2]
    let card1 = dateCard(day)
    let card2 = monthCard(month)
    let card3 = yearCard(year)
    let period1plus = minusTT(card1+card2)
    let period2plus = minusTT(card1+card3)
    let period3plus = minusTT(period1plus+period2plus)
    let period4plus = minusTT(card2+card3)
    let period1minus = minusTT(Math.abs(card1-card2))
    let period2minus = minusTT(Math.abs(card1-card3))
    let period3minus = minusTT(Math.abs(period1plus-period2plus))
    let period4minus = minusTT(Math.abs(card2-card3))
    let periods = [period1plus, period2plus, period3plus, period4plus]
    let black = [period1minus, period2minus, period3minus, period4minus ]
    commit('SET_PERIODS', periods)
    commit('SET_BLACK', black)
  },
  setAllArcans({ commit, state }) {
    const social = state.social
    const white = state.whiteKarma
    const gray = state.grayKarma
    const black = state.blackKarma
    const periods = state.periods
    let all = []
    for (let k of social) {
      if (!all.includes(k)) {
        all.push(k)
      }
    }
    // for (let k of white) {
    //   if (!all.includes(k)) {
    //     all.push(k)
    //   }
    // }
    if (!all.includes(white[0])) {
      all.push(white[0])
    }
    if (!all.includes(white[1])) {
      all.push(white[1])
    }
    for (let k of gray) {
      if (!all.includes(k)) {
        all.push(k)
      }
    }
    for (let k of black) {
      if (!all.includes(k)) {
        all.push(k)
      }
    }
    for (let k of periods) {
      if (!all.includes(k)) {
        all.push(k)
      }
    }

    if (all.includes("НЕТ")) {
      all.splice(all.indexOf("НЕТ"), 1)
    }
    all.sort(function(a, b) {
      return a - b;
    })
    commit('SET_ALLARCANS', all)
  },
  setPlanetsArr({commit}, payload) {
    let bdayArr = payload.split('.')
    let day = bdayArr[0]
    let month = bdayArr[1]
    let year = bdayArr[2]
    let arr = []
    arr[0] = addAllCiphers(day)
    arr[1] = addAllCiphers(month)
    arr[2] = addAllCiphers(addAllCiphers(year))
    arr[3] = addAllCiphers(addAllCiphers(arr[0], arr[1], arr[2]))
    arr[4] = addAllCiphers(arr[0]+arr[1])
    arr[5] = addAllCiphers(arr[0]+arr[2])
    arr[6] = addAllCiphers(arr[4]+arr[5])
    arr[7] = addAllCiphers(arr[1]+arr[2])
    arr[8] = Math.abs(arr[0]-arr[1])
    arr[9] = Math.abs(arr[0]-arr[2])
    arr[10] = Math.abs(arr[8]-arr[9])
    arr[11] = Math.abs(arr[2]-arr[1])
    commit('SET_PLANETSARR', arr)
  },
  setAges ({commit}, payload) {
    let bdayArr = payload.split('.')
    let day = bdayArr[0]
    let month = bdayArr[1]
    let year = bdayArr[2]
    let curDate = new Date()
    let date = curDate.getFullYear()
    let age = parseInt(date, 10)-parseInt(year, 10)
    if (parseInt(month, 10) > curDate.getMonth() + 1) {
      age-=1
    } else if (parseInt(month, 10) === curDate.getMonth() + 1 &&
        parseInt(day, 10) > curDate.getDate()) {
      age-=1
    }
    let ages = []
    ages[0] = age
    ages[1] = 36 - addAllCiphers(addAllCiphers(year, month, day))
    ages[2] = ages[1] + 9
    ages[3] = ages[2] + 9
    commit('SET_AGES', ages)
  },
  setAgeGap ({commit, state}) {
    let ageGap
    if (state.ages[0] < state.ages[1]) {
      ageGap = 1
    } else if (state.ages[0] < state.ages[2]) {
      ageGap = 2
    } else if (state.ages[0] < state.ages[3]) {
      ageGap = 3
    } else {
      ageGap = 4
    }
    commit('SET_AGE_GAP', ageGap)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
