<template>
  <div class="tab">
    <router-link
      class="tab_link"
      :to="'/'+to"
    >{{ name }}
    </router-link>
    <div
      class="tab_underline"
      :class="{active : this.$route.path === `/${to}`}"
    />
  </div>
</template>

<script>

export default {
  name: 'LinkTab',
  props: [ 'to', 'name' ]
}

</script>

<style lang="scss" scoped>

  .tab {
    width: fit-content;
    margin: 10px;
    &_link {
      color: white;
      text-decoration: none;
      font-family: Verdana;
    }
    &_underline {
      display: none;

    }
    &_underline.active {
      display: block;
      height: 5px;
      width: 100%;
      background-color: white;
    }
  }

</style>
