<template>
  <div class="planets">
    <div class="planets_container">
      <div class="planets_container-upper" @click="soul_description_visible = !soul_description_visible"><p>Число души</p><p v-if="planets_active">: {{soulNumber.number}}</p></div>
      <transition name="fadeHeight" mode="out-in">
        <div class="planets_container-lower" v-if="soulNumber.number" v-show="soul_description_visible">{{soulNumber.description}}</div>
      </transition>
    </div>
    <div class="planets_container">
      <div class="planets_container-upper" @click="destiny_description_visible = !destiny_description_visible"><p>Число судьбы</p><p v-if="planets_active">: {{destinyNumber.number}}</p></div>
      <transition name="fadeHeight" mode="out-in">
        <div class="planets_container-lower fadeHeight" v-if="destinyNumber.number" v-show="destiny_description_visible">{{destinyNumber.description}}</div>
      </transition>
    </div>
    <div class="planets_container">
      <div class="planets_container-upper" @click="name_description_visible = !name_description_visible"><p>Число имени</p><p v-if="planets_active">: {{nameNumber.number}}</p></div>
      <transition name="fadeHeight" mode="out-in">
        <div class="planets_container-lower fadeHeight" v-if="nameNumber.number" v-show="name_description_visible">{{nameNumber.description}}</div>
      </transition>
    </div>
    <div class="planets_container">
      <div class="planets_container-upper" @click="additional_description_visible = !additional_description_visible"><p>Дополнительно</p><p v-if="planets_active">: {{additionalNumber.number}}</p></div>
      <transition name="fadeHeight" mode="out-in">
        <div class="planets_container-lower fadeHeight" v-if="additionalNumber.number" v-show="additional_description_visible">{{additionalNumber.description}}</div>
      </transition>
    </div>
    <div class="planets_container" v-if="planets_active">
      <div class="planets_container-upper" @click="haldey_visible = !haldey_visible"><p>Халдейская нумерология</p></div>
      <transition name="fadeHeight" mode="out-in">
        <div class="planets_container-lower fadeHeight" v-if="additionalNumber.number" v-show="haldey_visible">
          <p>Число фамилии: {{surnameHaldey}}</p>
          <p>Число имени: {{nameHaldey}}</p>
          <p>Число отчества: {{patronymHaldey}}</p>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
    name: 'Planet',
    data () {
      return {
        soul_description_visible: false,
        destiny_description_visible: false,
        name_description_visible: false,
        additional_description_visible: false,
        haldey_visible: false
      }
    },
    computed: {
      ...mapGetters( 'mech', {
        getBday: 'getBday',
        name: 'getName',
        surname: 'getSurname',
        patronym: 'getPatronym'
      }),
      ...mapGetters( 'planet', {
        planets_active: 'getPlanetsActive',
        destinyNumber: 'getDestinyNumber',
        soulNumber: 'getSoulNumber',
        nameNumber: 'getNameNumber',
        additionalNumber: 'getAdditionalNumber',
        surnameHaldey: 'getSurnameHaldey',
        nameHaldey: 'getNameHaldey',
        patronymHaldey: 'getPatronymHaldey'
      })
    },
    methods: {
      ...mapActions('planet', {
        setPlanets: 'setPlanets',
        setHaldey: 'setHaldey'
      })
    },
  created () {
    if (this.getBday) {
        this.setPlanets({bday: this.getBday, name: this.getName})
        this.setHaldey({surname: this.surname, name: this.name, patronym: this.patronym })
      }
    }
  }
</script>

<style lang="scss">
.planets_container {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  user-select: none;

  &-upper {
    display: flex;
    width: 200px;
    justify-content: flex-start;
    border: 1px solid slateblue;
    margin-bottom: 5px;
    margin-top: 20px;
    padding: 3px 10px;
  }

  &-lower {
    white-space: pre-line;
    max-height: 10000px;
  }
}


.fadeHeight-enter-active {
  transition: all 0.5s ease;
}
.fadeHeight-enter-from {
  opacity: 0;
  max-height: 0;
}
.fadeHeight-enter-to {
  opacity: 1;
  max-height: 10000px;
}

.fadeHeight-leave-active {
  max-height: 0;
  transition: all 0.4s;
}


</style>
