import * as aux from './common'

function getDefaultState () {
  return {
    allArc: ["zero", "", "", "", "","", "", "", "","", "", "", "","", "", "", "","", ""]
  }
}

const state = () => ({
  allArc: ["zero", "", "", "", "","", "", "", "","", "", "", "","", "", "", "","", ""]
});

const getters = {
  getShanov: state => state.allArc
};

const mutations = {
  SET_SHANOV(state, payload) {
    state.allArc = payload
  }
};

const actions = {
  setShanov({ commit }, payload) {
    let bdayArr = payload.split('.')
    let day = bdayArr[0]
    let month = bdayArr[1]
    let year = bdayArr[2]
    let arcans = ["zero", "", "", "", "","", "", "", "","", "", "", "","", "", "", "","", ""]
    arcans[1] = aux.minusTT(day)
    arcans[2] = parseInt(month, 10)
    arcans[3] = aux.minusTT(aux.addAllCiphers(year))
    arcans[4] = aux.minusTT(parseInt(arcans[1], 10) + parseInt(arcans[2], 10))
    arcans[5] = aux.minusTT(parseInt(arcans[2], 10) + parseInt(arcans[3], 10))
    arcans[6] = aux.minusTT(parseInt(arcans[4], 10) + parseInt(arcans[5], 10))
    arcans[7] = aux.minusTT(parseInt(arcans[1], 10) + parseInt(arcans[5], 10))
    arcans[8] = aux.minusTT(parseInt(arcans[2], 10) + parseInt(arcans[6], 10))
    arcans[12] = aux.minusTT(parseInt(arcans[7], 10) + parseInt(arcans[8], 10))
    arcans[13] = aux.minusTT(parseInt(arcans[1], 10) + parseInt(arcans[4], 10) + parseInt(arcans[6], 10))
    arcans[14] = aux.minusTT(parseInt(arcans[3], 10) + parseInt(arcans[5], 10) + parseInt(arcans[6], 10))
    arcans[9] = aux.minusTT(parseInt(arcans[1], 10) - parseInt(arcans[2], 10))
    arcans[10] = aux.minusTT(parseInt(arcans[2], 10) - parseInt(arcans[3], 10))
    arcans[11] = aux.minusTT(parseInt(arcans[9], 10) - parseInt(arcans[10], 10))
    arcans[15] = aux.minusTT(parseInt(arcans[9], 10) + parseInt(arcans[10], 10) + parseInt(arcans[11], 10) - parseInt(arcans[7], 10))
    arcans[16] = "НЕТ"
    arcans[17] = aux.minusTT(parseInt(arcans[11], 10) + parseInt(arcans[6], 10))
    arcans[18] = aux.minusTT(parseInt(arcans[11], 10) + parseInt(arcans[8], 10))
    commit('SET_SHANOV', arcans)


  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
