import mech from './mech/index'
import main from './mech/main'
import loshu from './mech/loshu'
import alex from './mech/alex'
import roman from './mech/roman'
import shanov from './mech/shanov'
import prog from './mech/prog'
import planet from './mech/planet'

// import example from './module-example'

// Vue.use(Vuex)

import { createStore } from "vuex"

const store = createStore({
  modules: {
    mech,
    main,
    loshu,
    alex,
    roman,
    shanov,
    prog,
    planet
  }
})

export default store

