<script>
  import { mapGetters, mapActions } from 'vuex'
  export default {
    name: 'Hshanovskaya',
    data () {
      return {
        tooltips: []
      }
    },
    computed: {
      ...mapGetters('shanov', {
        getShanov: 'getShanov'
      }),
      ...mapGetters( 'mech', {
        getBday: 'getBday'
      })
    },
    methods: {
      ...mapActions('shanov', {
        setShanov: 'setShanov'
      }),
      openTooltip (num) {
        if (this.tooltips.includes(num)) {
          this.tooltips.splice(this.tooltips.indexOf(num))
        } else {
          this.tooltips.push(num)
        }
      }
    },
    created () {
      if (this.getBday !== "") {
        this.setShanov(this.getBday)
      }
    }
  }
</script>

<style lang="scss">
  .shanov-container {
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-wrap: wrap;
    .shanov-line {
      position: relative;
      display: flex;
      width: 100%;
      border-left: 1px solid black;
      border-top: 1px solid black;
      border-right: 1px solid black;
      overflow: visible;
      user-select: none;
      p {
        user-select: none;
      }
      &__number {
        text-align: center;
        width: 50%;
        border-right: 1px solid black;
        cursor: pointer;
        overflow: visible;
      }
      &__equal {
        text-align: center;
        width: 50%;
      }
      &__tooltip {
        z-index: 1;
        position: absolute;
        width: fit-content;
        max-width: 250px;
        height: fit-content;
        background-color: #f2c874;
        top: 10px;
        left: 10px;
        white-space: pre-wrap;
        cursor: pointer;
        p {
          margin: 3px 0;
          cursor: pointer;
        }
      }
    }
    .shanov-line:last-child {
      border-bottom: 1px solid black;
    }

  }
</style>
