import {addNums} from './auxillary'
import * as matrixWorks from '../data/Matrix.meanings.json'




//Функция, заполняющая количество цифр в квадратах для матрицы Александрова
const createSquare = function (...nums) {
    let strNum = 'n'
    let pith1 = 0
    let pith2 = 0
    let pith3 = 0
    let pith4 = 0
    let pith5 = 0
    let pith6 = 0
    let pith7 = 0
    let pith8 = 0
    let pith9 = 0
    for (let i = 0; i < nums.length; i++) {
        strNum += nums[i]
    }
    for (let i = 1; i < strNum.length; i++) {
        switch (strNum[i]) {
            case '1':
                pith1++;
                continue;
            case '2':
                pith2++;
                continue;
            case '3':
                pith3++;
                continue;
            case '4':
                pith4++;
                continue;
            case '5':
                pith5++;
                continue;
            case '6':
                pith6++;
                continue;
            case '7':
                pith7++;
                continue;
            case '8':
                pith8++;
                continue;
            case '9':
                pith9++;
        }
    }
    return {pith1, pith2, pith3, pith4, pith5, pith6, pith7, pith8, pith9}
}


const numSquare = function (d1, d2, d3) {
    let num1 = addNums(d1, d2, d3)
    let num2
    if (num1 == 10 || num1 == 11 || num1 == 12) {
        num2 = num1
    } else {
        num2 = addNums(num1)
    }
    let immed1
    if (parseInt(d1, 10) > 9) {
        immed1 = Math.floor(d1/10)
    } else {
        immed1 = +d1
    }
    let num3 = num1 - (immed1 * 2)
    let num4
    if (num3 == 10 || num3 == 11 || num3 == 12) {
        num4 = num3
    } else {
        num4 = addNums(num3)
    }

    var Pithagorean = createSquare(d1, d2, d3, num1, num2, num3, num4)


    //Дополнения к рабочим строкам
    let d3spec = parseInt(d3.slice(2, 4), 10)
    let strok2 = [9, num1, d3spec, parseInt(d1, 10)]
    strok2.sort((a, b) => a-b)
    let workSpec
    if(num2 > 9) {
        workSpec = num2
    } else {
        workSpec = num3
    }
    let strok3 = [26, 90, workSpec]
    strok3.sort((a, b) => a-b)
    strok3 = strok3.filter(point => !strok2.includes(point))
    strok2 = strok2.join(', ')
    strok3 = strok3.join(', ')

    let workNumsDesc = {}
    if(matrixWorks[num1 + '.' + num2]) {
        workNumsDesc.one = matrixWorks[num1 + '.' + num2].desc
        workNumsDesc.two =  matrixWorks[num2].desc
    } else if (matrixWorks[num2]) {
        workNumsDesc.two = matrixWorks[num2].desc
    }


    return {num1, num2, num3, num4, Pithagorean, strok2, strok3, workNumsDesc}
}





export default {numSquare}