import {addNums} from './auxillary'
import hald from './haldey'
// const fs = require('fs');
// const path = require('path')
import destiny from '../data/destiny_number.json'
import soul from '../data/soul_number.json'
import name from '../data/name_number.json'
import additional from '../data/additional_number.json'



// Расчет числа судьбы
const Count1 = function (d1, d2, d3) {
    var dF = addNums(d1, d2, d3)
    let numStr
    if(dF>9) {
        numStr = dF + ">" + addNums(dF)
        dF = addNums(dF)
    } else {
        numStr = dF
    }
    if(dF>9) {
        numStr = numStr + ">" + addNums(dF)
        dF = addNums(dF)
    }
    if(dF>9) {
        numStr = numStr + ">" + addNums(dF)
        dF = addNums(dF)
    }
    if(dF>9) {
        numStr = numStr + ">" + addNums(dF)
        dF = addNums(dF)
    }


    // let vedstring = fs.readFileSync(path.resolve(__dirname+'../../../public/Dest', 'Dest' + dF +'.txt'), 'utf8')
    let vedstring = destiny[dF]

    return {number: numStr, description: "Описание: " + vedstring};

}
//Рассчет числа души
const Count2 = function (d) {
    var dF = parseInt(d, 10)
    let numStr
    if(dF>9) {
        numStr = dF + ">" + addNums(dF)
        dF = addNums(dF)
    } else {
        numStr = dF
    }
    if(dF>9) {
        numStr = numStr + ">" + addNums(dF)
        dF = addNums(dF)
    }




    // let vedstring = fs.readFileSync(path.resolve(__dirname+'../../../public/Soul', 'soul' + dF +'.txt'), 'utf8')
    let vedstring = soul[dF]

    return {number: numStr, description: "Описание: " + vedstring};

}

//Расчет числа имени
const Count3 = function (word) {
    if (word == '') {
        return "не назначено"
    }
    let word_num = 0
    word = word.split('')

    word.forEach((letter) => {
        word_num += hald.haldToNumber(letter)
    })

    let numStr = word_num

    if(word_num>9) {
        numStr = word_num + ">" + addNums(word_num)
        word_num = addNums(word_num)
    } else {
        numStr = word_num
    }
    if(word_num>9) {
        numStr = numStr + ">" + addNums(word_num)
        word_num = addNums(word_num)
    }
    if(word_num>9) {
        numStr = numStr + ">" + addNums(word_num)
        word_num = addNums(word_num)
    }
    if(word_num>9) {
        numStr = numStr + ">" + addNums(word_num)
        word_num = addNums(word_num)
    }

    // let vedstring = fs.readFileSync(path.resolve(__dirname+'../../../public/Name', 'Name' + word_num +'.txt'), 'utf8')
    let vedstring = name[word_num]


    return {number: numStr, description: "Описание: " + vedstring};

}

//Рассчет числа души для выдачи дополнительных результатов
const Count4 = function (d) {
    var dF = parseInt(d, 10)
    let numStr
    if(dF>9) {
        numStr = dF + ">" + addNums(dF)
        dF = addNums(dF)
    } else {
        numStr = dF
    }
    if(dF>9) {
        numStr = numStr + ">" + addNums(dF)
        dF = addNums(dF)
    }




    // let vedstring = fs.readFileSync(path.resolve(__dirname+'../../../public/Dop', 'Dop' + dF +'.txt'), 'utf8')
    let vedstring = additional[dF]

    return {number: numStr, description: "Описание: " + vedstring};

}






export default {
    Count1, Count2, Count3, Count4
}
