import {negtwtw} from './auxillary'



let hald_rus = [
    ["а", "и", "с", "ъ"],
    ["б", "й", "т", "ы"],
    ["в", "к", "у", "ь"],
    ["г", "л", "ф", "э"],
    ["д", "м", "х", "ю"],
    ["е", "н", "ц", "я"],
    ["ё", "о", "ч"],
    ["ж", "п", "ш"],
    ["з", "р", "щ"]
]

let hald_eng = [
    ['a', 'j', 's'],
    ['b', 'k', 't'],
    ['c', 'l', 'u'],
    ['d', 'm', 'v'],
    ['e', 'n', 'w'],
    ['f', 'o', 'x'],
    ['g', 'p', 'y'],
    ['h', 'q', 'z'],
    ['i', 'r', ]
]



const haldToNumber = function (letter) {
    letter = letter.toLowerCase()
    let latina = /[\u0000-\u007f]/;

    if (latina.test(letter)) {

        for (let i = 0; i < hald_eng.length; i++) {
            for (let j = 0; j < hald_eng[i].length; j++) {
                if (letter == hald_eng[i][j]) {
                    return (i + 1)
                }
            }
        }
    } else {
        for (let i = 0; i < hald_rus.length; i++) {
            for (let j = 0; j < hald_rus[i].length; j++) {
                if (letter == hald_rus[i][j]) {
                    return (i + 1)
                }
            }

        }
    }
}



const haldWord = function (word) {
    if (word === '') {
        return "не назначено"
    }
    let word_num = 0
    word = word.split('')

    word.forEach((letter) => {
        word_num += haldToNumber(letter)
    })

    return (negtwtw(word_num))
}

export default {
    haldToNumber,
    haldWord
}
