<template>
  <div>

    <!--      //Table for social task-->
    <table v-if="social[0] !== ''">
      <tr>
        <td colspan="2">Социальная задача</td>
      </tr>
      <tr>
        <td>
          Карма прошлой жизни
        </td>
        <td :class="{'redlight' : black.includes(social[0])}"
            class="toolclick" @click="openTooltip('social0')"
        >
            <span
                :class="{shan : shanov.includes(social[0])}"
            >{{ social[0] }}</span>{{planets[planetsArr[0]]}}
          <div
              v-if="tooltips.includes('social0')"
              class="tooltip"
              @click="closeTooltip('social0', $event)"
          >
            <p>{{ arcan1[`${social[0]}`].arcanOpis }}</p>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          Карма и задачи Рода
        </td>
        <td :class="{'redlight' : black.includes(social[1])}"
            class="toolclick" @click="openTooltip('social1')"
        >
            <span :class="{shan : shanov.includes(social[1])}">
              {{ social[1] }}</span>{{planets[planetsArr[1]]}}
          <div
              v-if="tooltips.includes('social1')"
              class="tooltip"
              @click="closeTooltip('social1', $event)"
          >
            <p>{{ monthArcan[`${social[1]}`].descr }}</p>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          Талант для след. вопл.
        </td>
        <td :class="{'redlight' : black.includes(social[2])}">
            <span :class="{shan : shanov.includes(social[2])}">
              {{ social[2] }}</span>{{planets[planetsArr[2]]}}
        </td>
      </tr>
      <tr>
        <td>
          Задача на воплощение
        </td>
        <td :class="{'redlight' : black.includes(social[3])}"
            class="toolclick" @click="openTooltip('social3')"
        >
            <span :class="{shan : shanov.includes(social[3])}">
              {{ social[3] }}{{planets[planetsArr[3]]}}</span>
          <div
              v-if="tooltips.includes('social3')"
              class="tooltip"
              @click="closeTooltip('social3', $event)"
          >
            <p>{{ arcan3[`${social[3]}`].arcanOpis}}</p>
          </div>
        </td>
      </tr>
      <tr>
        <td>
          Жизненный путь
        </td>
        <td :class="{'redlight' : black.includes(social[4])}">
            <span :class="{shan : shanov.includes(social[4])}">
              {{ social[4] }}</span>
        </td>
      </tr>
      <tr>
        <td>
          Последний аркан
        </td>
        <td :class="{'redlight' : black.includes(social[5])}">
            <span :class="{shan : shanov.includes(social[5])}">
            {{ social[5] }}</span>
        </td>
      </tr>

    </table>



    <!--      //table for life periods-->
    <!--      //Life periods 29 38 47-->

    <table class="planetsTable" v-if="periods[0] !== ''">
      <tr>
        <td style="cursor: default;">
          Карты периодов жизни, возраст:{{ ages[0] }} лет
        </td>
        <td style="cursor: default;">
          Карты черной кармы
        </td>
      </tr>
      <tr>
        <td
            :class="{'redlight' : black.includes(periods[0]), 'agelight' : ages[0] < ages[1]}"
            @click="openPlanet(0, planetsArr[4])"
        >
            <span :class="{shan : shanov.includes(periods[0])}">
              {{periods[0]}}</span>{{planets[planetsArr[4]]}} до {{ages[1] -1}} лет
        </td>
        <td
            :class="{'redlight' : black.includes(black[0]), 'agelight' : ages[0] < ages[1]}"
            @click="openPlanet(1, planetsArr[8])"
        >
            <span :class="{shan : shanov.includes(black[0])}">
              {{black[0]}}</span>{{planets[planetsArr[8]]}}
        </td>
      </tr>
      <tr>
        <td
            :class="{'redlight' : black.includes(periods[1]), 'agelight' : (ages[0] >= ages[1]) && (ages[0] < ages[2])}"
            @click="openPlanet(0, planetsArr[5])"
        >
            <span :class="{shan : shanov.includes(periods[1])}">
              {{periods[1]}}</span>{{planets[planetsArr[5]]}} с {{ages[1]}} до {{ages[2] -1 }} лет
        </td>
        <td
            :class="{'redlight' : black.includes(black[1]), 'agelight' : (ages[0] >= ages[1]) && (ages[0] < ages[2])}"
            @click="openPlanet(1, planetsArr[9])"
        >
            <span :class="{shan : shanov.includes(black[1])}">
              {{black[1]}}</span>{{planets[planetsArr[9]]}}
        </td>
      </tr>
      <tr>
        <td
            :class="{'redlight' : black.includes(periods[2]), 'agelight' : (ages[0] >= ages[2]) && (ages[0] < ages[3])}"
            @click="openPlanet(0, planetsArr[6])"
        >
            <span :class="{shan : shanov.includes(periods[2])}">
              {{periods[2]}}</span>{{planets[planetsArr[6]]}} с {{ages[2]}} до {{ages[3]-1}} лет
        </td>
        <td
            :class="{'redlight' : black.includes(black[2]), 'agelight' : (ages[0] >= ages[2]) && (ages[0] < ages[3])}"
            @click="openPlanet(1, planetsArr[10])"
        >
            <span :class="{shan : shanov.includes(black[2])}">
              {{black[2]}}</span>{{planets[planetsArr[10]]}}
        </td>
      </tr>
      <tr>
        <td
            :class="{'redlight' : black.includes(periods[3]), 'agelight' : ages[0] >= ages[3]}"
            @click="openPlanet(0, planetsArr[7])"
        >
            <span :class="{shan : shanov.includes(periods[3])}">
              {{periods[3]}}</span>{{planets[planetsArr[7]]}} с {{ages[3]}} лет
        </td>
        <td
            :class="{'redlight' : black.includes(black[3]), 'agelight' : ages[0] >= ages[3]}"
            @click="openPlanet(1, planetsArr[11])"
        >
            <span :class="{shan : shanov.includes(black[3])}">
              {{black[3]}}</span>{{planets[planetsArr[11]]}}
        </td>
      </tr>
    </table>

    <div  v-if="social[0] !== ''">
      <div v-if="planetDescription !== ''">
        <transition
            name="slide"
            class="planet-tooltip"
        >
          <p>{{ planetDescription }}</p>
        </transition>
        <p @click="planetDescription = ''"
           style="font-weight: bold; cursor: pointer; user-select: none;">Закрыть</p>
      </div>
    </div>

    <!--      //table for karma-->

    <table v-if="white[0] !== ''">
      <tr>
        <td>
          Карты белой кармы
        </td>
        <td>
          Карты серой кармы
        </td>
      </tr>
      <tr>
        <td>
          Таланты характера
        </td>
        <td>
          Карты преткновения
        </td>
      </tr>
      <tr>
        <td :class="{'redlight' : black.includes(white[0])}">
            <span :class="{shan : shanov.includes(white[0])}">
              {{ white[0] }}</span>
        </td>
        <td :class="{'redlight' : black.includes(gray[0])}">
            <span :class="{shan : shanov.includes(gray[0])}">
              {{ gray[0] }}</span>
        </td>
      </tr>
      <tr>
        <td :class="{'redlight' : black.includes(white[1])}">
            <span :class="{shan : shanov.includes(white[1])}">
              {{ white[1] }}</span>
        </td >
        <td :class="{'redlight' : black.includes(gray[1])}">
            <span :class="{shan : shanov.includes(gray[1])}">
              {{ gray[1] }}</span>
        </td>
      </tr>
      <tr>
        <td>
          Таланты подсознания
        </td>
        <td>
          Точка препятствия
        </td>
      </tr>
      <tr>
        <td :class="{'redlight' : black.includes(white[2])}">
          {{ white[2] }}
        </td>
        <td :class="{'redlight' : black.includes(gray[2])}"
            class="toolclick" @click="openTooltip('gray2')"
        >
            <span :class="{shan : shanov.includes(gray[2])}">
              {{ gray[2] }}</span>
          <div
              v-if="tooltips.includes('gray2')"
              class="tooltip"
              @click="closeTooltip('gray2', $event)"
          >
            <p>{{ points[`${gray[2]}`].arcanOpis }}</p>
          </div>
        </td>
      </tr>
      <tr>
        <td :class="{'redlight' : black.includes(white[3])}"
            @click="openTooltip('white3')"
        >
          <span>{{ white[3] }}</span>
          <div
              v-if="tooltips.includes('white3')"
              class="tooltip"
              @click="closeTooltip('white3', $event)"
          >
            <p>{{ shanovText[`${white[3]}`].arcanOpis }}</p>
          </div>
        </td>

        <td :class="{'redlight' : black.includes(gray[3]), 'toolclick': gray[3] !== 'НЕТ'}"
            @click="openTooltip('gray3')"
        >
            <span :class="{shan : shanov.includes(gray[3])}">
              {{ gray[3] }}</span>
          <div
              v-if="tooltips.includes('gray3')"
              class="tooltip"
              @click="closeTooltip('gray3', $event)"
          >
            <p>{{ points[`${gray[3]}`].arcanOpis }}</p>
          </div>
        </td>
      </tr>
    </table>

    <app-karma-table :bday="bday" />

    <app-mega-table :bday="bday" />
  </div>
</template>

<script>
  import MegaTable from "@/components/main/MegaTable.vue";
  import KarmaTable from "@/components/main/KarmaTable.vue";
  import arcan1 from "@/info/Arcan1.json";
  import monthArcan from "@/info/month.json";
  import arcan3 from "@/info/Arcan3.json";
  import points from "@/info/points.json";
  import shanovText from "@/info/shanov.json";
  import PlanetGood from "@/info/PlanetGood.json";
  import PlanetBad from "@/info/PlanetBad.json";
  import {mapActions, mapGetters} from "vuex";

  export default {
    name: 'Romanova',
    components: {
      'app-mega-table': MegaTable,
      'app-karma-table': KarmaTable
    },
    data () {
      return {
        tooltips: [],
        arcan1: arcan1,
        monthArcan: monthArcan,
        arcan3: arcan3,
        points: points,
        shanovText: shanovText,
        planetary: [
          PlanetGood,
          PlanetBad
        ],
        planetDescription: ""
      }
    },
    computed: {
      ...mapGetters('mech', {
        bday: 'getBday',
        getName: 'getName',
        getSurname: 'getSurname',
        getPatronym: 'getPatronym',
        year: 'getYear',
        month: 'getMonth',
        day: 'getDay'
      }),
      ...mapGetters('shanov', {
        getShanov: 'getShanov'
      }),
      ...mapGetters('roman', {
        social: 'getSocial',
        white: 'getWhite',
        gray: 'getGray',
        black: 'getBlack',
        periods: 'getPeriods',
        allArcans: 'getAllArcans',
        planets: 'getPlanets',
        planetsArr: 'getPlanetsArr',
        ages: 'getAges',
        ageGap: 'getAgeGap'
      }),
      shanov () {
        let original = this.getShanov.slice(0, 6)
        let repeated = []
        let counted = 0
        console.log(this.getShanov)
        for (let i = 0; i<original.length; i++) {
          for (let j = i+1; j<original.length; j++) {
            if (original[i] === original[j] && original[i] !== 0) {
              console.log("IT IS", i, j, original)
              console.log("COUNTED before", counted)
              counted++
              console.log("COUNTED after", counted)
              original[j] = 0
            }
          }
          if (counted === 1) {
            repeated.push(original[i])
            console.log("NEW PUSHED", original[i], repeated)
            counted = 0
          } else {
            original[i] = 0
            counted = 0
          }
        }
        return repeated
      },
      name: {
        get() {
          return this.getName
        },
        set(value) {
          this.setName(value)
        }
      },
      surname: {
        get() {
          return this.getSurname
        },
        set(value) {
          this.setSurname(value)
        }
      },
      patronym: {
        get() {
          return this.getPatronym
        },
        set(value) {
          this.setPatronym(value)
        }
      }
    },
    methods: {
      ...mapActions('shanov', {
        setShanov: 'setShanov'
      }),
      ...mapActions('mech', {
        setName: 'setName',
        setSurname: 'setSurname',
        setPatronym: 'setPatronym'
      }),
      openTooltip (num) {
        if (this.tooltips.includes(num)) {
          this.tooltips = this.tooltips.filter(e => e !== num)
          // this.tooltips.splice(this.tooltips.indexOf(num))
        } else {
          this.tooltips.push(num)
        }
      },
      closeTooltip (num, e) {
        e.stopPropagation()
        this.tooltips = this.tooltips.filter(e => e !== num)
      },
      openPlanet(al, num) {
        this.planetDescription = this.planetary[al][num].data
      }
    },
    filters: {
      toPlanet (num) {
        while (num.toString().length > 1) {
          let k = 0
          num = num.toString()
          for(let i = 0; i<num.length; i++) {
            k+=parseInt(num[i], 10)
          }
          num = k
        }
        return num
      }
    }

  }
</script>


<style lang="scss">
table {
  width: 90%;
  max-width: 1000px;
  margin: 20px 0;
  tr {
    width: 100%;
    position: relative;
    td {
      width: 50%;
      border: 1px solid black;
      position: relative;
      span {
        width: fit-content;
      }
    }
  }
}

.tooltip {
  z-index: 1;
  position: absolute;
  width: fit-content;
  max-width: 250px;
  height: fit-content;
  background-color: #f2c874;
  top: 10px;
  left: 10px;
  white-space: pre-wrap;
  cursor: pointer;
  p {
    margin: 3px 0;
    cursor: pointer;
  }
}

.toolclick {
  cursor: pointer;
}

.agelight {
  background-color: #96fdff;
}

.planet-hover {
  cursor: pointer;
  user-select: none;
  &__header {
    font-weight: 600;
  }
  .planet-tooltip {
    width: 90%;
    max-width: 1000px;
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    p {
      width: 100%;
      max-width: 1000px;
      display: flex;
    }
  }
}

.slide-enter-active, .slide-leave-active {
  height: 300px;
  transition: all 0.3s ease-in-out;
  display: flex;
}

.slide-enter-to, .slide-leave {
  height: 300px;
  opacity: 1;
  display: flex;
}

.slide-enter, .slide-leave-to {
  overflow: hidden;
  height: 0;
  opacity: 0;
}

.planetsTable {
  td {
    cursor: pointer;
    user-select: none;
  }
}

.shan {
  border: 1px solid limegreen;
  border-radius: 100%;
  background-color: limegreen;
}
</style>