import vedic from '../utils/planet'
import haldey from '../utils/haldey'

function getDefaultState () {
  return {
    soulNumber: '',
    destinyNumber: '',
    nameNumber: '',
    additionalNumber: '',
    planetsActive: false,
    nameHald: '',
    surnameHald: '',
    fathernameHald: ''
  }
}

const state = () => ({
  soulNumber: {number: undefined, description: 'Не назначено'},
  destinyNumber: {number: undefined, description: 'Не назначено'},
  nameNumber: {number: undefined, description: 'Не назначено'},
  additionalNumber: {number: undefined, description: 'Не назначено'},
  planetsActive: false,
  nameHald: '',
  surnameHald: '',
  patronymHald: ''
});

const getters = {
  getSoulNumber: state => state.soulNumber,
  getDestinyNumber: state => state.destinyNumber,
  getNameNumber: state => state.nameNumber,
  getAdditionalNumber: state => state.additionalNumber,
  getPlanetsActive: state => state.planetsActive,
  getSurnameHaldey: state => state.surnameHald,
  getNameHaldey: state => state.nameHald,
  getPatronymHaldey: state => state.patronymHald,
};

const mutations = {
  SET_SOUL_NUMBER(state, payload) {
    state.soulNumber = payload
  },
  SET_DESTINY_NUMBER(state, payload) {
    state.destinyNumber = payload
  },
  SET_NAME_NUMBER(state, payload) {
    state.nameNumber = payload
  },
  SET_ADDITIONAL_NUMBER(state, payload) {
    state.additionalNumber = payload
  },
  SET_PLANETS_ACTIVE(state, payload) {
    state.planetsActive = payload
  },
  SET_SURNAME_HALDEY(state, payload) {
    state.surnameHald = payload
  },
  SET_NAME_HALDEY(state, payload) {
    state.nameHald = payload
  },
  SET_PATRONYM_HALDEY(state, payload) {
    state.patronymHald = payload
  }
};

const actions = {
  setPlanets({ commit }, payload) {
    let splitDate = payload.bday.split('.')
    const d1 = splitDate[0];
    const d2 = splitDate[1];
    const d3 = splitDate[2];
    let nDest = vedic.Count1(d1, d2, d3)
    let nSoul = vedic.Count2(d1)
    let nAdditional = vedic.Count4(d1)
    let nName
    if (payload.name === '' || !payload.name ) {
      nName = {number: undefined, description: "Не назначено"}
    } else {
      nName = vedic.Count3(payload.name)
    }
    commit('SET_SOUL_NUMBER', nSoul)
    commit('SET_DESTINY_NUMBER', nDest)
    commit('SET_NAME_NUMBER', nName)
    commit('SET_ADDITIONAL_NUMBER', nAdditional)
    commit('SET_PLANETS_ACTIVE', true)
  },
  setHaldey({ commit }, payload) {
    if ( payload.surname === '') {
      commit('SET_SURNAME_HALDEY', 'Фамилия не назначена')
    } else {
      commit('SET_SURNAME_HALDEY', haldey.haldWord(payload.surname))
    }
    if ( payload.name === '') {
      commit('SET_NAME_HALDEY', 'Имя не назначено')
    } else {
      commit('SET_NAME_HALDEY', haldey.haldWord(payload.name))
    }
    if ( payload.patronym === '') {
      commit('SET_PATRONYM_HALDEY', 'Отчество не назначено')
    } else {
      commit('SET_PATRONYM_HALDEY', haldey.haldWord(payload.patronym))
    }
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
