import arcan from '../utils/arcan-count'

function getDefaultState () {
  return {
    destiny_number1: 0,
    destiny_number2: 0,
    bad_time_periods: [],
    months: [],
    day_arcans: []
  }
}

const state = () => ({
  destiny_number1: 0,
  destiny_number2: 0,
  bad_time_periods: [],
  months: [],
  day_arcans: []
});

const getters = {
  getDestinyNumber1: state => state.destiny_number1,
  getDestinyNumber2: state => state.destiny_number2,
  getBadTimePeriods: state => state.bad_time_periods,
  getMonths: state => state.months,
  getDay: state => state.day_arcans
};

const mutations = {
  SET_DESTINY_NUMBER_1(state, payload) {
    state.destiny_number1 = payload
  },
  SET_DESTINY_NUMBER_2(state, payload) {
    state.destiny_number2 = payload
  },
  SET_BAD_TIME_PERIODS(state, payload) {
    state.bad_time_periods = payload
  },
  SET_MONTH_ARCANS(state, payload) {
    state.months[payload.month - 1] = {arcan1: payload.arcan1, arcan2: payload.arcan2}
  },
  SET_DAY_ARCANS(state, payload) {
    state.day_arcans = payload
  },
  RESET_STATE (state) {
    Object.assign(state, getDefaultState())
  }
};

const actions = {
  clear ({ commit }) {
    commit('RESET_STATE')
  },
  calculate_year ({commit}, payload) {
    const year = payload.year
    const birth_parts = payload.birthday.split(".")

    let d1 = birth_parts[0]
    let d2= birth_parts[1]
    let d3 = birth_parts[2]


    const birth_date = d1
    const birth_month = d2
    const birth_year= d3


    const destiny1 = arcan.Destiny1(birth_date, birth_month, year)
    const destiny2 = arcan.Destiny2(birth_date, birth_month, year)
    const periods = arcan.Periods(birth_date, birth_month, birth_year)
    const bad_time_periods = [periods.period1minus, periods.period2minus, periods.period3minus, periods.period4minus]

    commit('SET_DESTINY_NUMBER_1', destiny1)
    commit('SET_DESTINY_NUMBER_2', destiny2)
    commit('SET_BAD_TIME_PERIODS', bad_time_periods)

  },
  calculate_months({commit}, payload) {
    const year = payload.year
    const birth_parts = payload.birthday.split(".")

    let d1 = birth_parts[0]
    let d2= birth_parts[1]
    for(let month=1; month<13; month++) {
      const arcan1 = arcan.CalMonth1(d1, d2, year, month)
      const arcan2 = arcan.CalMonth2(d1, d2, year, month)

      commit('SET_MONTH_ARCANS', {month: month, arcan1: arcan1, arcan2: arcan2})
    }
  },
  calculate_day({commit}, payload) {
    const [date, month, year] = [payload.date, payload.month, payload.year]
    const birth_parts = payload.birthday.split(".")

    let d1 = birth_parts[0]
    let d2= birth_parts[1]



    let day1 = arcan.CalDay1(d1, d2, date, month, year)
    let day2 = arcan.CalDay2(d1, d2, date, month, year)
    let day3 = arcan.CalDay3(d1, d2, date, month, year)
    console.log(day3)
    commit('SET_DAY_ARCANS', [day1, day2, day3])
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
