<template>
  <div>
    <app-mask
      @accept:unmasked="changeDate"
      @accept:masked="changeStoredDate"
      v-model="birthDate"
      :mask="'00.00.0000'"
    />


  </div>
</template>

<script>
  // import { TheMask } from 'vue-the-mask'
  import { IMaskComponent } from 'vue-imask'
  import { mapActions, mapGetters } from 'vuex';

  export default {
    name: 'dateField',
    components: {
      'app-mask': IMaskComponent
    },
    data () {
      return {
        birthDate: "",
      }
    },
    computed: {
      ...mapGetters('mech', {
        bday: 'getBday'
      }),
    },
    methods: {
       changeDate (unmaskedValue) {
        if (!unmaskedValue || unmaskedValue.length < 8) {
          return null
        } else  {
           this.setBirthday(this.birthDate)
           this.setSocial(this.bday)
           this.setPeriods(this.bday)
           this.setKarma(this.bday)
           this.setPlanetsArr(this.bday)
           this.setAllArcans()
           this.setAges(this.bday)
           this.setAgeGap()
           this.setShanov(this.bday)


           // this.setKarmaRole()
        }
      },
      changeStoredDate (maskedValue) {
         this.birthDate = maskedValue
      },
      ...mapActions('mech', {
          setBirthday: 'setBirthday'
        }
      ),
      ...mapActions('roman', {
        setSocial: 'setSocial',
        setPeriods: 'setPeriods',
        setKarma: 'setKarma',
        setAllArcans: 'setAllArcans',
        setKarmaRole: 'setKarmaRole',
        setPlanetsArr: 'setPlanetsArr',
        setAges: 'setAges',
        setAgeGap: 'setAgeGap'
      }),
      ...mapActions('shanov', {
        setShanov: 'setShanov'
      })
    },
    beforeMount() {
      this.birthDate = this.bday
    }
  }
</script>

<style lang="scss" scoped>
div {
  display: flex;
  input {
    margin: auto 0;
  }
}
</style>

