<template>
<!--  <div class="container">-->
<!--    <div class="container-small">-->

    <div class="main_inputs">
      <div class="input_container">
        <app-field />
        <p>Дата рождения</p>
      </div>

      <div class="input_container">
        <input v-model="surname" />
        <p>Фамилия</p>
      </div>

      <div class="input_container">
        <input v-model="name" />
        <p>Имя</p>
      </div>

      <div class="input_container">
        <input v-model="patronym" />
        <p>Отчество</p>
      </div>

    </div>

<!--    </div>-->
<!--  </div>-->
</template>

<script>
import DateField from '@/components/main/DateField.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'main',
  components: {
    'app-field': DateField
  },
  computed: {
    ...mapGetters('mech', {
      bday: 'getBday',
      getName: 'getName',
      getSurname: 'getSurname',
      getPatronym: 'getPatronym',
      year: 'getYear',
      month: 'getMonth',
      day: 'getDay'
    }),
    name: {
      get() {
        return this.getName
      },
      set(value) {
        this.setName(value)
      }
    },
    surname: {
      get() {
        return this.getSurname
      },
      set(value) {
        this.setSurname(value)
      }
    },
    patronym: {
      get() {
        return this.getPatronym
      },
      set(value) {
        this.setPatronym(value)
      }
    }
  },
  methods: {
    ...mapActions('mech', {
      setName: 'setName',
      setSurname: 'setSurname',
      setPatronym: 'setPatronym'
    })
  }
}
</script>

<style lang="scss" scoped>
  .main_inputs {
    display: flex;
    flex-direction: column;
    width: 300px;
    .input_container {
      display: flex;
      justify-content: center;
      align-items: stretch;
      height: 40px;
      width: 100%;
      p {
        font-weight: bold;
        height: fit-content;
        margin: auto auto auto 10px;
      }
      input {
        width: 177px;
        height: 21px;
        margin: auto 0 auto 0;
      }
    }
  }


</style>
