// const fs = require('fs');
import {addNums, negtwtw, negNine} from './auxillary'

import * as myarcan1 from '../data/Arcan1.json'
import * as myarcan2 from '../data/month.json'
import * as myarcan3 from '../data/Arcan3.json'
import * as points from '../data/points.json'










// Функция рассчета первого аркана
const Count1 = function (d1) {
    let Arcane1
    if (d1 > 22) {
        Arcane1 = d1 - 22;
    } else {
        Arcane1 = d1;
    }
    let getArcan = myarcan1[parseInt(Arcane1, 10)]
    return {one: getArcan,
            two: 'Описание: ' + getArcan.arcanOpis}

    //  parseInt(Arcane1, 10)

}

// Функция рассчета второго аркана:
const Count2 = function (d1, d2, d3) {
    let Arcane2 = addNums(d1, d2, d3)
    Arcane2 = negtwtw(Arcane2);
    let getArcan = myarcan1[parseInt(Arcane2, 10)]
    return (getArcan)

}

// Функция рассчета третьего аркана:
const Count3 = function (d1, d2, d3) {
    var num1 = negtwtw(parseInt(d1, 10))
    var num2 = parseInt(d2, 10)
    let num3 = negtwtw(addNums(d3))

    let Arcane3 = negtwtw(num1 + num2 + num3)
    let getArcan = myarcan3[parseInt(Arcane3, 10)]
    let nameArcan = myarcan1[parseInt(Arcane3, 10)]
    return {one: getArcan,
            two: "Описание: " + getArcan.arcanOpis}

}





//Функция вывода информации о человеке по месяцу рождения
const monthInf = function (d2) {
    let getArcan = myarcan2[parseInt(d2, 10)];
    return {one: getArcan.name, two: 'Задача рода: ' + getArcan.descr}
}







//Рассчет точек препятствия
const Count5 = function(d1, d2) {
    d1 = parseInt(d1, 10)
    d2 = parseInt(d2, 10)
    let prep1
    let prep2
    let getArcan1
    let getArcan2
    let arPrep1 = {}
    let arPrep2 = {}
    if(d1>9) {
        if (d1 > 22) {
            prep1 = negtwtw(d1+d2)
            getArcan1 = points[prep1]
            arPrep1 = {one: getArcan1, two: 'Описание: '+getArcan1.arcanOpis}
            arPrep2 = {one: 'Точка препятствия отсутствует', two: ''}

        } else {
            prep2 = d1;
            prep1 = negtwtw(d1+d2)
            getArcan1 = points[prep1]
            getArcan2 = points[prep2]
            arPrep1 = {one: getArcan1, two: 'Описание: '+getArcan1.arcanOpis}
            arPrep2 = {one: getArcan2, two: 'Описание: '+getArcan2.arcanOpis}
        }

    } else {
        prep1 = negtwtw(d1+d2)
        getArcan1 = points[prep1]
        arPrep1 = {one: getArcan1, two: 'Описание: '+getArcan1.arcanOpis}
        arPrep2 = {one: 'Точка препятствия отсутствует', two: ''}
    }
    return {arPrep1, arPrep2}

}



const DateCard = function(d) {
    d = parseInt(d, 10)
    let Arcane1
    if (d > 22) {
        Arcane1 = d - 22;
    } else {
        Arcane1 = d;
    }
    return Arcane1
}

const MonthCard = function(d) {
    return parseInt(d, 10)
}

const YearCard = function(d) {
    let strNum = 'n'
    for (let i = 0; i < d.length; i++) {
        strNum += d[i]
    }
    let countAllIn = 0
    for (let i = 1; i < strNum.length; i++) {
        countAllIn += parseInt(strNum[i], 10)
    }
    return negtwtw(countAllIn)
}


const Destiny1 = function(d1, d2, d3) {
    let full = "n"
    full = full + d1 + d2 + d3
    console.log(full)
    let endsum = 0
    for (let i = 1; i < full.length; i++) {
        endsum += parseInt(full[i], 10)
    }
    return negtwtw(endsum)
}


const Destiny2 = function(d1, d2, d3) {
    let card1 = DateCard(d1)
    let card2 = MonthCard(d2)
    let card3 = YearCard(d3)
    let cardsum = card1+card2+card3
    return negtwtw(cardsum)

}


const Destiny3 = function(d1, d2, d3) {

    return negtwtw(addNums(d1)+addNums(d2)+addNums(addNums(d3)))

}

const Gift1 = function(d1, d2, d3) {
    let num1 = DateCard(d1)
    let num2 = Destiny1(d1, d2, d3)
    let numsum = negtwtw(num1+num2)
    return numsum
}

const Gift2 = function(d1, d2, d3) {
    let num1 = DateCard(d1)
    let num2 = Destiny2(d1, d2, d3)
    let numsum = negtwtw(num1+num2)
    return numsum
}

const Problem1 = function(d1, d2, d3) {
    let num1 = DateCard(d1)
    let num2 = Destiny1(d1, d2, d3)
    let numsum = Math.abs(num1-num2)
    if (numsum == 0) {
        numsum = 22
    }
    return numsum
}

const Problem2 = function(d1, d2, d3) {
    let num1 = DateCard(d1)
    let num2 = Destiny2(d1, d2, d3)
    let numsum = Math.abs(num1-num2)
    if (numsum == 0) {
        numsum = 22
    }
    return numsum
}

const Periods = function(d1, d2, d3) {
    let card1 = DateCard(d1)
    let card2 = MonthCard(d2)
    let card3 = YearCard(d3)
    let period1plus = negtwtw(card1+card2)
    let period2plus = negtwtw(card1+card3)
    let period3plus = negtwtw(period1plus+period2plus)
    let period4plus = negtwtw(card2+card3)
    let period1minus = negtwtw(Math.abs(card1-card2))
    let period2minus = negtwtw(Math.abs(card1-card3))
    let period3minus = negtwtw(Math.abs(period1minus-period2minus))
    let period4minus = negtwtw(Math.abs(card2-card3))




    return {
        period1plus,
        period2plus,
        period3plus,
        period4plus,
        period1minus,
        period2minus,
        period3minus,
        period4minus
        
    }
}


const LifeTimes = function(d1, d2, d3) {
    let lifetime1 = 36 - addNums(addNums(addNums(d1, d2, d3)))
    let lifetime2 = lifetime1 + 9 
    let lifetime3 = lifetime2 + 9

    return {lifetime1, lifetime2, lifetime3}

}


const CalMonth1 = function(d1, d2, d3, month) {
    return negtwtw(addNums(d1)+addNums(d2)+addNums(d3)+addNums(month))


}

const CalMonth2 = function(d1, d2, d3, month) {
    return negtwtw(negtwtw(parseInt(d1, 10))+parseInt(d2)+negtwtw(addNums(d3))+parseInt(month))

}

const CalDay1 = function(d1, d2, d3, d4, d5) {
    return negtwtw(addNums(d1)+addNums(d2)+addNums(d3)+addNums(d4)+addNums(d5))

}

const CalDay2 = function(d1, d2, d3, d4, d5) {
    return negtwtw(negtwtw(d1)+parseInt(d2, 10)+negtwtw(d3)+parseInt(d4, 10)+negtwtw(addNums(d5)))

}

const CalDay3 = function(d1, d2, d3, d4, d5) {
    return negtwtw(negNine(d1)+negNine(d2)+negNine(d3)+negNine(d4)+negNine(d5))
}









export default {
    Count1,
    Count2,
    Count3,
    Count5,
    monthInf,
    DateCard,
    MonthCard,
    YearCard,
    Destiny1,
    Destiny2,
    Destiny3,
    Gift1,
    Gift2,
    Problem1,
    Problem2,
    Periods,
    LifeTimes,
    CalMonth1,
    CalMonth2,
    CalDay1,
    CalDay2,
    CalDay3
}