<template>
  <div class="alex_container">
    <div class="alex_top" @click="firstRowDescriptionShow = !firstRowDescriptionShow">
      <p>Первая строка: {{workNumbers}}</p>
      <div v-show="firstRowDescriptionShow">
        <p>Первое рабочее число — показывает какие именно качества человек должен в себе развить для достижения поставленной цели;</p>
        <p>Второе рабочее число — это цель воплощения, а так же его ведущее качество;</p>
        <p>Третье дополнительное число — это качества, данные человеку от рождения (они во многом определяются родителями).</p>
        <p>Четвертое дополнительное число — это основное качество, полученное человеком от рождения.</p>
        <p>{{workNumsDesc.one}}</p>
        <p>{{workNumsDesc.two}}</p>
      </div>
    </div>
    <div>
      <p>Вторая строка: {{secondRow}}</p>
      <p>Третья строка: {{thirdRow}}</p>
    </div>
    <div>
      <table class="squareTable">
        <tbody>
        <tr class="row1">
          <td class="td1">{{cell1}}</td>
          <td class="td2">{{cell4}}</td>
          <td class="td3">{{cell7}}</td>
        </tr>
        <tr class="row2">
          <td class="td1">{{cell2}}</td>
          <td class="td2">{{cell5}}</td>
          <td class="td3">{{cell8}}</td>
        </tr>
        <tr class="row3">
          <td class="td1">{{cell3}}</td>
          <td class="td2">{{cell6}}</td>
          <td class="td3">{{cell9}}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div>
      <p>Возможные переходы</p>
      <p v-if="cell2 > 2">22 -> 6 или 7 при духовной работе</p>
      <p v-if="cell1 > 11 && cell8 === 'Нет'">11 -> 8, усиление 7 и +2 или +4</p>
      <p v-if="cell1 > 11">111 -> 11111 в стрессе</p>
      <p v-if="cell2 > 22">22 -> 4</p>
      <p v-if="cell4 !== 'Нет'">4 -> 22</p>
      <p v-if="cell3 > 3">33 (остается) -> +5</p>
      <p v-if="cell9 > 9">99 (остается) -> +5</p>
      <p v-if="cell6 > 6">6 -> 7; +4 или +22 </p>
      <p v-if="cell7 > 7">7 -> 6; -4 или -22 </p>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from "vuex";

export default {
  name: 'Alexandrov',
  data () {
    return {
      firstRowDescriptionShow: false
    }
  },
  methods: {
    ...mapActions('alex', {
      calculateAlex: 'calculate_alex'
    })
  },
  computed: {
    ...mapGetters('mech', {
      bday: 'getBday'
    }),
    ...mapGetters('alex', {
      cell1: 'getCell1',
      cell2: 'getCell2',
      cell3: 'getCell3',
      cell4: 'getCell4',
      cell5: 'getCell5',
      cell6: 'getCell6',
      cell7: 'getCell7',
      cell8: 'getCell8',
      cell9: 'getCell9',
      workNumbers: 'getWorkNumbers',
      workNumsDesc: 'getWorkNumsDesc',
      secondRow: 'getSecondRow',
      thirdRow: 'getThirdRow'
    })
  },
  created () {
    if (this.bday !== '') {
      this.calculateAlex({bday: this.bday})
    }
  }
}
</script>


<style lang="scss">
.alex_top {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  user-select: none;
  border: 1px solid slateblue;
  margin-bottom: 5px;
  margin-top: 20px;
  padding: 3px 10px;
  max-width: 500px;
  width: 95%;
}

.squareTable {

  margin: 10px;
  border: none;
  display: inline;
}

.squareTable tr td {
  width: 140px;
  height: 140px;
  border: 1px solid teal;
  text-align: center;
}

.squareTable .row1 .td1,
.squareTable .row2 .td1,
.squareTable .row3 .td1 {
  border-left: none;
}

.squareTable .row1 .td1,
.squareTable .row1 .td2,
.squareTable .row1 .td3 {
  border-top: none;
}

.squareTable .row1 .td3,
.squareTable .row2 .td3,
.squareTable .row3 .td3 {
  border-right: none;
}

.squareTable .row3 .td1,
.squareTable .row3 .td2,
.squareTable .row3 .td3 {
  border-bottom: none;
}
</style>
