const addNums = function (...dates) {
    let strNum = 'n'
    for (let i = 0; i < dates.length; i++) {
        strNum += dates[i]
    }
    let countAllIn = 0
    for (let i = 1; i < strNum.length; i++) {
        countAllIn += parseInt(strNum[i], 10)
    }
    return countAllIn
}

//Функция отнятия 22 пока не останется меньше 23
const negtwtw = function (num) {
    num = parseInt(num, 10)
    if (num===0) {
        num=22
    }
    if (num > 22) {
        num -= 22
    }
    if (num > 22) {
        return negtwtw(num)
    } else {
        return num;
    }


}

const negNine = function (num) {
    num = parseInt(num, 10)
    if (num === 0) {
        num = 9
    }
    if (num > 9) {
        num = addNums(num)
    }
    if (num > 9) {
        return negNine(num)
    } else {
        return num
    }
}




module.exports = {
    addNums, negtwtw, negNine
}
