<template>
  <div>






<!--    //Diagram for male\female cards percentage-->

    <!--    <pie-chart v-if="bday" :data="[['Женские', graphPercent[0]], ['Мужские',graphPercent[1]]]"></pie-chart>-->

  <!--      //Table for percentage-->

  <table v-if="bday !== ''">
<!--    <tr>-->
<!--      <td>-->
<!--        Формула-->
<!--      </td>-->
<!--      <td>-->
<!--        Главный аркан-->
<!--      </td>-->
<!--      <td>-->
<!--        Второстепенный аркан-->
<!--      </td>-->
<!--    </tr>-->
    <tr>
      <td colspan="3">
        Баланс энергий
      </td>
    </tr>
    <tr>
      <td>
        Мужская
      </td>
      <td>
        <span v-for="(num, i) in male" :key="i" :class="{'redlight' : black.includes(num)}">{{ num }}&nbsp;</span>
      </td>
      <td>
        {{percentage[0]}}%
      </td>
    </tr>
    <tr>
      <td>
        Женская
      </td>
      <td>
        <span v-for="(num, i) in female" :key="i" :class="{'redlight' : black.includes(num)}">{{ num }}&nbsp;</span>
      </td>
      <td>
        {{percentage[1]}}%
      </td>
    </tr>
    <tr>
      <td>
        Нейтральная м
      </td>
      <td>
        <span v-for="(num, i) in neutralM" :key="i" :class="{'redlight' : black.includes(num)}">{{ num }}&nbsp;</span>
      </td>
      <td>
        {{percentage[2]}}%
      </td>
    </tr>
    <tr>
      <td>
        Нейтральная ж
      </td>
      <td>
        <span v-for="(num, i) in neutralF" :key="i" :class="{'redlight' : black.includes(num)}">{{ num }}&nbsp;</span>
      </td>
      <td>
        {{percentage[3]}}%
      </td>
    </tr>
  </table>


   <div
    v-if="greatData !== null"
    class="megaTable"
   >

    <table
      v-for="(category, index) in greatData"
      :key="index"
    >
      <tr
        @click="toggleMega(index)"
      >
        <td class="nameRow" colspan="3">{{ category.name }}</td>
      </tr>
        <tr
          v-for="(line, i) in category.data"
          :key="i"
          class="dataRow"
        >
          <td v-if="megas.includes(index)">{{ line.name }}</td>
          <td v-if="megas.includes(index)">
            <span
              v-for="(num, i) in line.mainCards"
              :key="i"
              :class="{'redlight' : black.includes(num)}"
            >
              {{ num }}&nbsp;</span>
          </td>
          <td v-if="megas.includes(index)">
            <span
              v-for="(num, i) in line.secondaryCards"
              :key="i"
              :class="{'redlight' : black.includes(num)}"
            >
              {{ num }}&nbsp;
            </span>
          </td>
        </tr>

    </table>
   </div>

  </div>
</template>

<script>
// import Vue from 'vue'
import { mapGetters } from 'vuex'
// import VueChartkick from 'vue-chartkick'
// import Chart from 'chart.js'
import MegaAspects from './MegaAspects.json'

// Vue.use(VueChartkick.use(Chart))

export default {
  name: 'MegaTable',
  props: [ 'bday' ],
  data () {
    return {
      arcansMale: [1,4,5,7,9,11,15,16,19],
      arcansFemale: [2,3,6,12,13,14,17,18,21],
      arcansNeutralM: [10, 22],
      arcansNeutralF: [8, 20],
      designations: [
        {"Ро": "Рождения"},
        {"Су": "Судьбы"},
        {"Пе": "Периоды"},
        {"Да": "Даров"},
        {"Ка": "Кармы"},
        {"Тп": "Препятствия"},
        {"Кп": "Преткновения"}],
      megas: []
    }
  },
  computed: {
    ...mapGetters( 'roman', {
      social: 'getSocial',
      white: 'getWhite',
      gray: 'getGray',
      black: 'getBlack',
      periods: 'getPeriods',
      allArcans: 'getAllArcans'
    }),
    male () {
      return this.arcansMale.filter( n => this.allArcans.includes(n))
    },
    female () {
      return this.arcansFemale.filter( n => this.allArcans.includes(n))
    },
    neutralM () {
      return this.arcansNeutralM.filter( n => this.allArcans.includes(n))
    },
    neutralF () {
      return this.arcansNeutralF.filter( n => this.allArcans.includes(n))
    },
    percentage () {
      let perc = [0, 0, 0, 0]
      perc[0] = Math.floor((this.male.length / this.allArcans.length) * 100)
      perc[1] = Math.floor((this.female.length / this.allArcans.length) * 100)
      perc[2] = Math.floor((this.neutralM.length / this.allArcans.length) * 100)
      perc[3] = Math.floor((this.neutralF.length / this.allArcans.length) * 100)
      return perc
    },
    graphPercent () {
      return [this.percentage[1]+this.percentage[3], this.percentage[0]+this.percentage[2]]
    },
    greatData () {
      if (this.social[0] !== '') {
        let fullData = []
        let i = 0
        let counter2 = 0
        let line = {name: "", mainCards: [], secondaryCards: []}
        for (let dataPart of MegaAspects) {
          fullData.push({ name: dataPart.name, data: [] })
          for (let dataLine of dataPart.data) {
            //Creating the data of this part

            line.name = dataLine.name

            //Основные карты

            if (dataLine.partsMain.includes("Ро")) {
              if (dataLine.mainCards.includes(this.social[0]) && !this.black.includes(this.social[0])) {
                line.mainCards.push(this.social[0])
              }
            }

            if (dataLine.partsMain.includes("Су")) {
              if (dataLine.mainCards.includes(this.social[3])  && !this.black.includes(this.social[3])) {
                line.mainCards.push(this.social[3])
              }
              if (dataLine.mainCards.includes(this.social[4])  && !this.black.includes(this.social[4])) {
                line.mainCards.push(this.social[4])
              }
            }

            if (dataLine.partsMain.includes("Пе")) {
              if (dataLine.mainCards.includes(this.periods[0]) && !this.black.includes(this.periods[0])) {
                line.mainCards.push(this.periods[0])
              }
              if (dataLine.mainCards.includes(this.periods[1]) && !this.black.includes(this.periods[1])) {
                line.mainCards.push(this.periods[1])
              }
              if (dataLine.mainCards.includes(this.periods[2]) && !this.black.includes(this.periods[2])) {
                line.mainCards.push(this.periods[2])
              }
              if (dataLine.mainCards.includes(this.periods[3]) && !this.black.includes(this.periods[3])) {
                line.mainCards.push(this.periods[3])
              }
            }

            if (dataLine.partsMain.includes("Да")) {
              if (dataLine.mainCards.includes(this.white[0]) && !this.black.includes(this.white[0])) {
                line.mainCards.push(this.white[0])
              }
              if (dataLine.mainCards.includes(this.white[1]) && !this.black.includes(this.white[1])) {
                line.mainCards.push(this.white[1])
              }
            }

            if (dataLine.partsMain.includes("Ка")) {
              if (dataLine.mainCards.includes(this.black[0])) {
                line.mainCards.push(this.black[0])
              }
              if (dataLine.mainCards.includes(this.black[1])) {
                line.mainCards.push(this.black[1])
              }
              if (dataLine.mainCards.includes(this.black[2])) {
                line.mainCards.push(this.black[2])
              }
              if (dataLine.mainCards.includes(this.black[3])) {
                line.mainCards.push(this.black[3])
              }
            }

            if (dataLine.partsMain.includes("Тп")) {
              if (dataLine.mainCards.includes(this.gray[0])) {
                line.mainCards.push(this.gray[0])
              }
              if (dataLine.mainCards.includes(this.gray[1])) {
                line.mainCards.push(this.gray[1])
              }
            }

            if (dataLine.partsMain.includes("Кп")) {
              if (dataLine.mainCards.includes(this.gray[2])) {
                line.mainCards.push(this.gray[2])
              }
              if (dataLine.mainCards.includes(this.gray[3])) {
                line.mainCards.push(this.gray[3])
              }
            }

            //Дополнительные карты

            if (dataLine.partSecond.includes("Ро")) {
              if (dataLine.secondaryCards.includes(this.social[0]) && !this.black.includes(this.social[0])) {
                line.secondaryCards.push(this.social[0])
              }
            }

            if (dataLine.partSecond.includes("Су")) {
              if (dataLine.secondaryCards.includes(this.social[3]) && !this.black.includes(this.social[3])) {
                line.secondaryCards.push(this.social[3])
              }
              if (dataLine.secondaryCards.includes(this.social[4]) && !this.black.includes(this.social[4])) {
                line.secondaryCards.push(this.social[4])
              }
            }

            if (dataLine.partSecond.includes("Пе")) {
              if (dataLine.secondaryCards.includes(this.periods[0]) && !this.black.includes(this.periods[0])) {
                line.secondaryCards.push(this.periods[0])
              }
              if (dataLine.secondaryCards.includes(this.periods[1]) && !this.black.includes(this.periods[1])) {
                line.secondaryCards.push(this.periods[1])
              }
              if (dataLine.secondaryCards.includes(this.periods[2]) && !this.black.includes(this.periods[2])) {
                line.secondaryCards.push(this.periods[2])
              }
              if (dataLine.secondaryCards.includes(this.periods[3]) && !this.black.includes(this.periods[3])) {
                line.secondaryCards.push(this.periods[3])
              }
            }

            if (dataLine.partSecond.includes("Да")) {
              if (dataLine.secondaryCards.includes(this.white[0]) && !this.black.includes(this.white[0])) {
                line.secondaryCards.push(this.white[0])
              }
              if (dataLine.secondaryCards.includes(this.white[1]) && !this.black.includes(this.white[1])) {
                line.secondaryCards.push(this.white[1])
              }
            }

            if (dataLine.partSecond.includes("Ка")) {
              if (dataLine.secondaryCards.includes(this.black[0])) {
                line.secondaryCards.push(this.black[0])
              }
              if (dataLine.secondaryCards.includes(this.black[1])) {
                line.secondaryCards.push(this.black[1])
              }
              if (dataLine.secondaryCards.includes(this.black[2])) {
                line.secondaryCards.push(this.black[2])
              }
              if (dataLine.secondaryCards.includes(this.black[3])) {
                line.secondaryCards.push(this.black[3])
              }
            }

            if (dataLine.partSecond.includes("Тп")) {
              if (dataLine.secondaryCards.includes(this.gray[0])) {
                line.secondaryCards.push(this.gray[0])
              }
              if (dataLine.secondaryCards.includes(this.gray[1])) {
                line.secondaryCards.push(this.gray[1])
              }
            }

            if (dataLine.partSecond.includes("Кп")) {
              if (dataLine.secondaryCards.includes(this.gray[2])) {
                line.secondaryCards.push(this.gray[2])
              }
              if (dataLine.secondaryCards.includes(this.gray[3])) {
                line.secondaryCards.push(this.gray[3])
              }
            }

            line.mainCards = [...new Set(line.mainCards)];

            if (dataLine.neededMain-1 < line.mainCards.length) {
              if (dataLine.neededSecond > line.secondaryCards.length) {
                line.secondaryCards = []
              }
              fullData[i].data.push(line)
            }

            line = {name: "", mainCards: [], secondaryCards: []}

            counter2 = counter2 + 1
          }
          if (fullData[i].data.length === 0) {
            fullData.pop()
          } else {
            i+=1
          }
        }

        return fullData
      } else {
        return null
      }
    }
  },
  methods: {
    toggleMega (i) {
      if (this.megas.includes(i)) {
        this.megas = this.megas.filter(e => e !== i)
      } else {
        this.megas.push(i)
      }
    }
  }
}
</script>

<style lang="scss" scoped>

  table {
    td {
      border: 1px solid black;
    }
  }

  .chart {
    width: 250px;
    height: 250px;
    .inner-chart {
      /*background-color: #406cff;*/
    }
  }

  .megaTable {
    width: 90%;
    max-width: 1000px;
    table {
      width: 100%;
      tr {
        width: 100%;
        td {
          width: 30%;
        }
        .nameRow {
          width: 100%;
          background-color: orange;
        }
      }
    }
  }


</style>
