

const addAllCiphers = (...nums) => {
  let all = "n"
  for (let k of nums) {
    all+=k
  }
  let sum = 0
  for (let i = 1; i < all.length; i++ ) {
    sum += parseInt(all[i], 10)
  }
  return sum
}


const addAllNums = (...nums) => {
  let all = 0
  for (let k of nums) {
    all+= parseInt(k)
  }
  return all
}



const minusTT = (num) => {
  num = parseInt(num, 10)
  while (num > 22) {
    num -= 22
  }
  if (num === 0) {
    num = 22
  }
  if (num < 0) {
    num *= -1
  }
  return parseInt(num, 10)
}

const arcanus = (num) => {
  num = parseInt(num, 10)
  while (num > 22) {
    num -= 22
  }
  if (num === 0) {
    num = 22
  }
  if (num < 0) {
    num += 22
  }
  return parseInt(num, 10)
}


const addCiphers = (ciph) => {
  let whole = "n"
  for(let i = 0; i < ciph.length; i++) {
    whole += ciph[i]
  }
  let sum = 0
  for(let i = 1; i < whole.length; i++){
    sum += parseInt(whole[i], 10)
  }
  return sum
}


const dateCard = function(d) {
  d = parseInt(d, 10)
  let Arcane1
  if (d > 22) {
    Arcane1 = d - 22;
  } else {
    Arcane1 = d;
  }
  return Arcane1
}

const monthCard = function(d) {
  return parseInt(d, 10)
}

const yearCard = function(d) {
  let strNum = 'n'
  for (let i = 0; i < d.length; i++) {
    strNum += d[i]
  }
  let countAllIn = 0
  for (let i = 1; i < strNum.length; i++) {
    countAllIn += parseInt(strNum[i], 10)
  }
  return minusTT(countAllIn)
}



export {
  addAllCiphers,
  addAllNums,
  minusTT,
  addCiphers,
  dateCard,
  monthCard,
  yearCard,
  arcanus
}
