<template>
  <div class="prognoz">
    <div class="prognoz_top">
      <input v-model="calculatedYear" placeholder="Год рассчетов" />
      <button @click='calculate'>Рассчитать!</button>
    </div>
    <div v-if="calculationsFinished" class="prognoz_table">
      <div class="prognoz_years">
        <p>Арканы года - <span :class="{'bad': bad_time_periods.includes(destiny1)}">{{destiny1}}</span>,
          <span :class="{'bad': bad_time_periods.includes(destiny2)}">{{destiny2}}</span></p>
      </div>
      <div class="prognoz_months">
        <div
            v-for="(month, month_index) in month_names"
            :key="month_index"
            class="months_container"
            :class="{'months_container-selected': selectedMonth === month_index+1}"
            @click="setMonth(month_index)"
        >
          <p class="months_container-top">{{month}}</p>
          <div class="months_container-bottom">
            <p :class="{'bad': bad_time_periods.includes(month_arcans[month_index].arcan1)}">{{month_arcans[month_index].arcan1}}</p>,
            <p :class="{'bad': bad_time_periods.includes(month_arcans[month_index].arcan2)}">{{month_arcans[month_index].arcan2}}</p></div>
        </div>
      </div>
      <div class="prognoz_calendar" v-if="selectedMonth !== 0">
        <div
            class="prognoz_calendar-weekday"
            v-for="(weekday, num) in week_names"
            :key="num"
        >
          {{weekday}}
        </div>
        <div
            class="prognoz_calendar-date"
            :class="[day === ' ' ? 'hidden' : 'visible', {'active': day === selectedDate}]"
            v-for="(day, index) in month_dates"
            :key="index"
            @click="setDay(day)"
        >
          {{day}}
        </div>
      </div>
      <div v-if="day_arcans.length" class="prognoz_dayarcans">
        <p
            v-for="(arcan, index) in day_arcans"
            :class="{'bad': bad_time_periods.includes(arcan)}"
            :key="index"
        >
          {{arcan}}&nbsp;
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: 'Prognoz',
  data () {
    return {
      month_names: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
      week_names: ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'],
      calculatedYear: '',
      selectedMonth: 0,
      selectedDate: 0,
      calculationsFinished: false,
      month_dates: []
    }
  },
  computed: {
      ...mapGetters('mech', {
        bday: 'getBday'
      }),
    ...mapGetters('prog', {
      destiny1: 'getDestinyNumber1',
      destiny2: 'getDestinyNumber2',
      bad_time_periods: 'getBadTimePeriods',
      month_arcans: 'getMonths',
      day_arcans: 'getDay'
    })
  },
  methods: {
      ...mapActions('prog', {
        clear_state: 'clear',
        calculate_year: 'calculate_year',
        calculate_months: 'calculate_months',
        calculate_day: 'calculate_day'
      }),
    calculate() {
      this.clear_state()
      this.calculate_year({birthday: this.bday, year: this.calculatedYear})
      this.calculate_months({birthday: this.bday, year: this.calculatedYear})

      this.calculationsFinished = true
      },
    setMonth(month_index) {
      this.selectedMonth = month_index+1
      let dateString = this.calculatedYear.toString()+'-'+this.selectedMonth.toString()+'-01'
      let first_day = new Date(dateString)
      let first_day_of_the_week = first_day.getDay()
      if (first_day_of_the_week === 0) {
        first_day_of_the_week = 7
      }
      let amount_of_days = new Date(this.calculatedYear, this.selectedMonth, 0).getDate();
      let empty_array = Array(first_day_of_the_week-1).fill(' ')
      let days_array = Array.from({length: amount_of_days}, (_, i) => i + 1)
      this.month_dates = empty_array.concat(days_array);
    },
    setDay(day) {
      this.selectedDate = day
      this.calculate_day({birthday: this.bday, date: day, month: this.selectedMonth, year: this.calculatedYear})
    }
  }
}
</script>


<style lang="scss">
.prognoz_table {
  display: flex;
  flex-direction: column;
}

.prognoz_months {
  width: 80%;
  max-width: 600px;
  height: 300px;
  display: flex;
  justify-content: space-around;
  align-content: space-around;
  flex-wrap: wrap;

  .months_container {
    width: 100px;
    height: 50px;
    padding: 5px;
    border: 2px solid #e4decd;
    background-color: #acb7ae;
    display: flex;
    flex-direction: column;
    flex-basis: 30%;
    cursor: pointer;
    user-select: none;
    &-top {
      margin: 0;
      height: 50%;
    }
    &-bottom {
      margin: 0;
      display: flex;
      height: 50%;
      width: 100%;
      justify-content: flex-start;
      p {
        margin: 0;
        height: 0;
      }
    }
    &-selected {
      border: 4px solid #E2D1F9;
    }
  }
}

.prognoz_calendar {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 80%;
  max-width: 600px;
  height: 300px;
  align-content: space-between;
  &-weekday {
    width: 15px;
    height: 30px;
    background-color: #92A8D1;
    border: 2px solid #F7CAC9;
    flex-basis: 12%;
    margin-right: 5px;
  }
  &-date {
    flex-basis: 12%;
    width: 15px;
    height: 30px;
    margin-right: 5px;
    padding: 5px 10px 5px 10px;
    cursor: pointer;
    user-select: none;
    &.hidden {
      opacity: 0;
    }
    &.visible {
      background-color: #DFCFBE;
      border: 2px solid #F7CAC9;
    }
    &.active {
      border: 2px solid #007FFF;
    }
  }
}

.bad {
  color: red;
}

.prognoz_dayarcans {
  display: flex;
  flex-direction: row;
  width: 200px;
  .bad {
    color:red;
  }
}


</style>
