function getDefaultState () {
  return {
    bday: "",
    day: "",
    month: "",
    year: ""
  }
}

const state = () => ({
  bday: "",
  name: "",
  surname: "",
  patronym: "",
  day: "",
  month: "",
  year: ""
});

const getters = {
  getBday: state => state.bday,
  getName: state => state.name,
  getSurname: state => state.surname,
  getPatronym: state => state.patronym,
  getYear: state => state.year,
  getMonth: state => state.month,
  getDay: state => state.day
};

const mutations = {
  SET_BIRTHDAY(state, payload) {
    state.bday = payload
  },
  SET_NAME(state, payload) {
    state.name = payload
  },
  SET_SURNAME(state, payload) {
    state.surname = payload
  },
  SET_PATRONYM(state, payload) {
    state.patronym = payload
  },
  SET_DAY(state, payload) {
    state.day = payload
  },
  SET_MONTH(state, payload) {
    state.month = payload
  },
  SET_YEAR(state, payload) {
    state.year = payload
  }
};

const actions = {
  setBirthday({ commit }, payload) {
    // let bday = payload.slice(0, 2)+'.'+payload.slice(2, 4)+'.'+payload.slice(4)
    let bday = payload
    commit('SET_BIRTHDAY', bday)
    let bdayArr = bday.split('.')
    commit('SET_DAY', bdayArr[0])
    commit('SET_MONTH', bdayArr[1])
    commit('SET_YEAR', bdayArr[2])
  },
  setName({ commit }, payload) {
    commit('SET_NAME', payload)
  },
  setSurname({ commit }, payload) {
    commit('SET_SURNAME', payload)
  },
  setPatronym({ commit }, payload) {
    commit('SET_PATRONYM', payload)
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
