import alex from '../utils/alex'

function getDefaultState () {
  return {
    cell1: "Нет",
    cell2: "Нет",
    cell3: "Нет",
    cell4: "Нет",
    cell5: "Нет",
    cell6: "Нет",
    cell7: "Нет",
    cell8: "Нет",
    cell9: "Нет",
    workNumbers: "Нет",
    secondRow: '',
    thirdRow: '',
    workNumsDesc: {one: '', two: ''},
    firstRowDescriptionShow: false
  }
}

const state = () => ({
  cell1: "Нет",
  cell2: "Нет",
  cell3: "Нет",
  cell4: "Нет",
  cell5: "Нет",
  cell6: "Нет",
  cell7: "Нет",
  cell8: "Нет",
  cell9: "Нет",
  workNumbers: "Нет",
  secondRow: '',
  thirdRow: '',
  workNumsDesc: {one: '', two: ''},
  firstRowDescriptionShow: false
});

const getters = {
  getCell1: state => state.cell1,
  getCell2: state => state.cell2,
  getCell3: state => state.cell3,
  getCell4: state => state.cell4,
  getCell5: state => state.cell5,
  getCell6: state => state.cell6,
  getCell7: state => state.cell7,
  getCell8: state => state.cell8,
  getCell9: state => state.cell9,
  getWorkNumbers: state => state.workNumbers,
  getWorkNumsDesc: state => state.workNumsDesc,
  getSecondRow: state => state.secondRow,
  getThirdRow: state => state.thirdRow
};

const mutations = {
  SET_CELL(state, payload) {
    state['cell'+payload.cellnum] = payload.data
  },
  SET_SECOND_ROW(state, payload) {
    state.secondRow = payload
  },
  SET_THIRD_ROW(state, payload) {
    state.thirdRow = payload
  },
  SET_WORK_NUMBERS(state, payload) {
    state.workNumbers = payload
  },
  SET_WORK_NUMS_DESC(state, payload) {
    state.workNumsDesc = payload
  }
};

const actions = {
  calculate_alex({commit}, payload) {

    let bday = payload.bday
    let splitDate = bday.split('.')
    const d1 = splitDate[0];
    const d2 = splitDate[1];
    const d3 = splitDate[2];
    let pithRaw = alex.numSquare(d1, d2, d3)

    commit('SET_SECOND_ROW', pithRaw.strok2)

    commit('SET_THIRD_ROW', pithRaw.strok3)

    let CellSquare = pithRaw.Pithagorean
    commit("SET_WORK_NUMBERS", pithRaw.num1+", "+pithRaw.num2+", "+pithRaw.num3+", "+pithRaw.num4)
    commit("SET_WORK_NUMS_DESC", pithRaw.workNumsDesc)
    commit('SET_CELL', {data: CellSquare.pith1!=0 ? "1".repeat(CellSquare.pith1) : "Нет", cellnum: 1})
    commit('SET_CELL', {data: CellSquare.pith2!=0 ? "2".repeat(CellSquare.pith2) : "Нет", cellnum: 2})
    commit('SET_CELL', {data: CellSquare.pith3!=0 ? "3".repeat(CellSquare.pith3) : "Нет", cellnum: 3})
    commit('SET_CELL', {data: CellSquare.pith4!=0 ? "4".repeat(CellSquare.pith4) : "Нет", cellnum: 4})
    commit('SET_CELL', {data: CellSquare.pith5!=0 ? "5".repeat(CellSquare.pith5) : "Нет", cellnum: 5})
    commit('SET_CELL', {data: CellSquare.pith6!=0 ? "6".repeat(CellSquare.pith6) : "Нет", cellnum: 6})
    commit('SET_CELL', {data: CellSquare.pith7!=0 ? "7".repeat(CellSquare.pith7) : "Нет", cellnum: 7})
    commit('SET_CELL', {data: CellSquare.pith8!=0 ? "8".repeat(CellSquare.pith8) : "Нет", cellnum: 8})
    commit('SET_CELL', {data: CellSquare.pith9!=0 ? "9".repeat(CellSquare.pith9) : "Нет", cellnum: 9})
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
